      var player = videojs("videojs-vr-player");    // ()里面是播放器id
      player.mediainfo = player.mediainfo || {};
      player.mediainfo.projection = "360";
      player.vr({
        projection: "AUTO",
        debug: true,
        forceCardboard: false  
      });
    })(window, videojs);
